body {
  margin: 0;
  padding: 0;
  font-family: 'Space Mono', monospace;
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.login-page {
    width: 100vw;
    height: 100vh;
    background-color:lightseagreen;
    color: #FCFCFC;
}

.login-wrapper {
    position: absolute;
    width: 80%;
    max-width: 414px;
    left: 0px;
    right: 0px;
    top: 60px;
    margin: 0 auto;
}

.login-title {
    width: 134px;
    height: 93px;
    color: #FCFCFC;
}

.login-subtitle {
    width: 226px;
    height: 42px;
    font-size: 14px;
    line-height: 18px;
    display: flex;
    align-items: center;
    color: #FCFCFC;
    margin: 10% 0;
}

.login-input {
    width: 100%;
    height: 30px;
    margin-bottom: 2%;
    font-size: 12px;
    color: #000000;
}

.login-details {
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    padding: 2% 0;
}

.form-wrapper {
    width: 100%;
    max-width: 340px;
    margin: 0 auto;
    padding: 0 3%;
    font-size: 13px;
}

.form-item {
    padding: 10px 0;
} 

.form-item h4 {
    font-weight: 500;
}

.form-radio label:last-child {
    margin-left: 10%;
}

.nes-input, .nes-textarea {
    border-image-width: 1;
}

.nes-input {
    padding: 0;
    margin: 0;
}
.header {
    background: transparent;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    font-size: 0.8rem;
    overflow: hidden;
    z-index: 20;
    display: flex;
    align-items: left;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
  
  .is-fixed {
    position: fixed;
    background: #fff;
    -webkit-transition: background-color 0.4s ease;
    transition: background-color 0.4s ease;
    box-shadow: 0 10px 40px 0 rgba(3,38,61,0.06);
  }
  
  .logo-container {
    display: block;
    padding: 1rem 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .header.is-open {
    height: 100%;
    background-color: #30C2FF;
    -webkit-transition: background-color 0.4s ease;
    transition: background-color 0.4s ease;
    padding-bottom: 3rem;
    position: fixed;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  
  .header.is-open .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  
  .header.is-open .navigation {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
  }
  
  .header.is-open .navigation-menu {
    flex: 1 1;
  }
  
  .header.is-open .navigation {
    margin-bottom: 3rem;
    flex: 1 1;
  }
 
  .header.is-open .logo-container {
    position: absolute;
    left: 0rem;
  }
  
  .navigation {
    display: none;
  }
  
  .navigation a {
    text-decoration: none;
    color: #fff;
    display: flex;
    align-items: center;
  }

  .navigation ul {
    list-style-type: none;
    -webkit-padding-start: 20px;
            padding-inline-start: 20px;
  }

  .navigation a>svg {
    padding-right: 10px;
  }

  .nav-menu-item:not(.is-hidden) {
    margin-bottom: 2rem;
  }
  
  .hamburger-holder {
    position: absolute;
    right: 1.6rem;
    top: 0;
  }
  
  .hamburger {
    height: 60px;
    width: 30px;
    position: relative;
    display: block;
    border: none;
    background-color: transparent;
  }
  
  .line {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 2px;
    background-color: #03263D;
    -webkit-transform-origin: center;
            transform-origin: center;
  }
  
  .line:first-child {
    top: calc(50% - 3px);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  } 
  
  .line:last-child {
    top: calc(50% + 3px);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  } 
  
  .is-open .line:first-child {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    top: 50%;
  }
  
  .is-open .line:last-child {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 50%;
  }
  
  @media screen and (max-width: 768px) {
    .header {
      height: 60px;
    }
  }
  
  @media screen and (min-width: 1025px) { 
    .header.is-fixed {
      padding-top: 0.9rem;
      padding-bottom: 0.9rem;
      min-height: 1px;
    }
  
    .header .container {
      display: flex;
      align-items: center;
      margin: 0 auto;
    }
  
    .hamburger {
      display: none;
    }
  
    .navigation {
      position: static;
      position: initial;
      display: flex;
      height: auto;
      background-color: transparent;
      flex: 1 1;
    }
  
    .navigation a {
      text-decoration: none;
      color: #03263D;
      display: flex;
      align-items: center;
    }

    .nav-menu {
      display: flex;
      align-items: center;
    }
  
    .nav-menu-item:not(.is-hidden) {
      margin-right: 4rem;
      margin-bottom: 0;
    }
  
    .nav-menu-item:last-child {
      margin-right: 0;
    }
  }


.section-wrapper {
    position: absolute;
    width: 100%;
    max-width: 414px;
    left: 0px;
    right: 0px;
    top: 60px;
    margin: 0 auto;
} 

.section-body {
    font-size: 14px;
    line-height: normal;
    color: #979797;
}

.info-wrapper {
    padding: 1rem;
    position: relative;
}

.subtitle {
    font-size: 10px;
    line-height: normal;
    color: #979797;
    margin: 0;
}

.title {
    width: 320px;
    margin: 0;
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
    color: #000000;
}

.date {
    font-size: 18px;
    line-height: normal;
    color: #30C2FF;
    margin: 0;
}

.btn-wrapper {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-wrapper button {
    width: 250px;
    height: 42px;
    background: #92CE41;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-size: 14px;
    line-height: normal;
    text-align: center;
    color: #FFFFFF;
    font-family: 'Space Mono', monospace;
}

.details-list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 10px 5px;
}

.details-items {
    display: flex;
    justify-content: center;
    font-size: 12px;
    color: #000000;
    margin: 0;
    padding: 0;
}

.details-items p {
    margin: 0;
    margin-left: 5px;
}



@media screen and (min-width: 1025px) {
    .App {
        width: 40%;
        height: 100%;
        background-color: orange;
    }
}
